header {
    /* border: 1px solid rgb(0, 255, 34); */
    width: 100%;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0%;
    transition: opacity 1.5s;
}

.hide-header {
    opacity: 0%;
}

.show-header {
    opacity: 100%;
}

.title_header {
    /* border: 1px solid black; */
    width: 500px;
    height: 250px;
    background-image: url("./title_header.png");
    background-size: 100%;
    background-position: center center;
    background-repeat: no-repeat;
}